/* ------------------------------ RADIO BUTTONS ON/OFF -------------------------------------------- */

div[data-radio-button="off"] ul._pi_answers_container li a span._pi_radio_button_outer {
  display: none !important;
}

div[data-radio-button="on"] ul._pi_answers_container li a span._pi_radio_button_outer {
  display: block !important;
}

/* ------------------------------ TEXT ALIGNMENT: RADIO AND CHECKBOX ------------------------------ */

div[data-radio-button="off"] ul._pi_answers_container li a label {
  text-align: center !important;
}

div[data-radio-button="on"] ul._pi_answers_container li a label {
  text-align: left !important;
}

div[data-answer-widths="fixed"] ul._pi_answers_container li label._pi-control-checkbox {
  text-align: left !important;
}

/* ------------------------------ BUTTON GROUP ALIGNMENT: FIXED, VARIABLE LEFT/RIGHT ------------- */

div[data-answer-widths="variable-center"] ul._pi_answers_container {
  -ms-flex-pack: center;
  justify-content: center;
}

div[data-answer-widths="variable-center"] ul._pi_answers_container li {
  margin: 5px 1% !important;
}

div[data-answer-widths="variable-left"] ul._pi_answers_container {
  -ms-flex-pack: start;
  justify-content: flex-start;
}

div[data-answer-widths="variable-left"] ul._pi_answers_container li {
  margin: 5px 1% !important;
}

div[data-answer-widths="fixed"] ul._pi_answers_container {
  -ms-flex-pack: start;
  justify-content: flex-start;
}

div[data-answer-widths="fixed"] ul._pi_answers_container li {
  margin: 5px 0.9% !important;
}

/* ------------------------------ BUTTON WIDTHS: VARIABLE ---------------------------------------- */

div[data-answer-widths="variable-left"] ul._pi_answers_container li,
div[data-answer-widths="variable-center"] ul._pi_answers_container li {
  /* 	flex: 0 0 auto; */
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -ms-flex-preferred-size: auto;
  flex-basis: auto;
}

/* 	flex: 1 0 auto;	} */

